<template>
    <div class="text-center">
        <v-dialog
            v-model="dialog"
            max-width="800"
            @click:outside="changeDialogContact(false)"
            @keydown.esc="changeDialogContact(false)"
        >
            <v-card class="pa-10">
                <div class="d-flex justify-center">
                    <span class="text-uppercase font-weight-bold text-h5 mb-4">
                        contact me
                    </span>
                </div>

                <div class="">
                    <v-form ref="form" v-model="valid" class="">
                        <v-row>
                            <v-col cols="12" md="6" class="py-0">
                                <v-text-field
                                    v-model="mailData.name"
                                    :rules="nameRules"
                                    label="Name"
                                    color="#6755e7"
                                    solo
                                    required
                                ></v-text-field>
                            </v-col>
                            <v-col cols="12" md="6" class="py-0">
                                <v-text-field
                                    v-model="mailData.email"
                                    :rules="emailRules"
                                    label="Email"
                                    color="#6755e7"
                                    solo
                                    required
                                ></v-text-field>
                            </v-col>
                            <v-col cols="12" class="py-0">
                                <v-text-field
                                    v-model="mailData.title"
                                    :rules="titleRules"
                                    label="Title"
                                    color="#6755e7"
                                    solo
                                    required
                                ></v-text-field>
                            </v-col>
                            <v-col cols="12" class="py-0">
                                <v-textarea
                                    v-model="mailData.message"
                                    :rules="messageRules"
                                    solo
                                    name="input-7-4"
                                    label="Message"
                                ></v-textarea>
                            </v-col>
                        </v-row>

                        <v-btn
                            class="my-2"
                            color="#6755e7"
                            x-large
                            dark
                            block
                            @click="addData"
                        >
                            send
                        </v-btn>
                    </v-form>
                </div>

                <v-card-actions>
                    <v-spacer></v-spacer>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <v-dialog v-model="dialogAlert" hide-overlay persistent width="400">
            <v-card
                color=""
                dark
                height=""
                class="d-flex justify-center align-center pt-2"
            >
                <v-card-text class="d-flex flex-column align-center">
                    <span class="mb-2"> Please wait </span>
                    <v-progress-linear
                        indeterminate
                        rounded
                        color="white"
                        height="8"
                        class="mb-0"
                    ></v-progress-linear>
                </v-card-text>
            </v-card>
        </v-dialog>
        <v-dialog
            v-model="dialogSuccess"
            persistent
            max-width="400"
            transition="scroll-y-transition"
        >
            <v-card class="pb-6">
                <v-card-title class="headline d-flex justify-center">
                    <v-icon size="150" color="#a5dc86">
                        mdi-check-circle-outline
                    </v-icon>
                </v-card-title>
                <v-card-text class="text-center">
                    <span class="text-h4 font-weight-bold"> Good job! </span
                    ><br />
                    <span> Message sent successfuly </span>
                </v-card-text>
                <v-card-actions class="d-flex justify-center">
                    <v-btn color="primary" @click="dialogSuccess = false">
                        ok
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
import slug from "slug";
import { db, fb } from "@/components/firebaseInit";
import { mapGetters, mapActions } from "vuex";
export default {
    name: "ContactDialog",

    data() {
        return {
            dialogSuccess: false,
            dialogAlert: false,
            valid: true,
            name: "",
            nameRules: [
                (v) => !!v || "Name is required",
                (v) =>
                    (v && v.length <= 20) ||
                    "Name must be less than 20 characters",
            ],
            email: "",
            emailRules: [
                (v) => !!v || "E-mail is required",
                (v) => /.+@.+\..+/.test(v) || "E-mail must be valid",
            ],
            title: "",
            titleRules: [(v) => !!v || "Message is required"],
            message: "",
            messageRules: [(v) => !!v || "Message is required"],
            mailData: {
                email: "",
                name: "",
                title: "",
                message: "",
                slug: "",
            },
        };
    },

    computed: {
        ...mapGetters({
            dialog: "getDialogContact",
        }),
    },

    methods: {
        ...mapActions({
            changeDialogContact: "updateDialogContact",
        }),
        validate() {
            return this.$refs.form.validate();
            // this.$refs.form.reset();
            // this.$refs.form.resetValidation()
        },
        async addData() {
            if (this.validate() == true) {
                this.dialogAlert = true;
                await this.slugMail(5);
                await this.timeStamp();
                db.collection("inbox_mail")
                    .add(this.mailData)
                    .then((docRef) => {
                        setTimeout(() => {
                            this.dialogAlert = false;
                            this.dialogSuccess = true;
                        }, 2000);
                        console.log("Document written with ID: ", docRef.id);
                    })
                    .catch((error) => {
                        console.error("Error adding document: ", error);
                    });
                this.changeDialogContact(false);
            } else {
                alert("Form salah");
            }
        },
        async slugMail(q) {
            let text = " ";
            let chars = "abcdefghijklmnopqrstuvwxyz1234567890";

            for (let i = 0; i < q; i++) {
                text += chars.charAt(Math.floor(Math.random() * chars.length));
            }
            const aslug = this.mailData.title + text;
            this.mailData.slug = slug(aslug);
        },
        async timeStamp() {
            this.mailData.created_at = fb.firestore.FieldValue.serverTimestamp();
        },
    },
};
</script>

<style scoped>
</style>